@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100&display=swap);
body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #000;
  cursor: pointer;
}

code {
  font-family: 'Poppins', sans-serif;
}

.Navbar {
    padding: 20px;
    background: #C60967;
    color: white;
    font-size: 30px;
    font-weight: 600;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
}
.left{float: left;
width: 90%;}
.right{float: right;
display: -webkit-flex;
display: flex;
-webkit-justify-content: flex-end;
        justify-content: flex-end;}
.right img{width: 10%;}
@media screen and (max-width:450px) {
    .Navbar{font-size: 15px;}

    
}
p {
    margin: 0;
}

.Card {
    background: #fff;
    border-radius: 10px;
}

.Card__img {
    text-align: center;
}
.Card__img img{width: 40%;}
.Card__types {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;    
}

.Card__type {
    padding: 5px 10px;
    margin: 10px 10px 10px 0;
    border-radius: 5px;
    background: cyan;
    color: #fff;
    font-weight: 600;
    text-align: center;
    width: 75px;
}

.Card__name {
    text-align: center;
    text-transform: capitalize;
    font-weight: 800;
}

.Card__info {
    margin-left: 10px;
}

.Card__data {
    padding-bottom: 10px;
}

.Card__data .title {
    font-weight: 700;
    color: #000;
}
.Card__data p{
    font-weight: 600;
    color: #C60967;
    font-size: 20px;
}


* {
    box-sizing: border-box;
}

.grid-container {
    width: 70%;
    display: grid;
    margin: 0 auto;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    grid-gap: 10px;
}

.btn {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    margin: 20px 0;
}

.btn button {
    border-style: none;
    padding: 5px 10px;
    margin-right: 10px;
    border-radius: 5px;
    background: #C60967;
    color: #fff;
    font-size: 1em;
}
@media screen and (max-width:450px) {
    .grid-container{width: 100%;
    margin: 0px;}

    
}
